// common mixins - start

@mixin mkdfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin mkdfImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include mkdfAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;
            transition: opacity .2s ease-in-out;
        }

    } @else {
        @include mkdfAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        transition: opacity .2s ease-in-out;
    }
}

@mixin mkdfSVGbg($color: $first-main-color) {
    transition: opacity .2s ease-out;

    .mkdf-active-hover-middle {
        position: absolute;
        top: 0;
        left: 8px;
        width: calc(100% - 16px);
        height: 100%;
        border-radius: 4px;
        background-color: $color;
    }

    svg {
        position: absolute;
        top: 0;

        path {
            fill: $color;
            stroke: $color;
        }

        &.mkdf-active-hover-left {
            left: 0;
        }

        &.mkdf-active-hover-right {
            right: 0;
        }
    }
}

@mixin mkdfStandardPaginationStyle($list_type: null) {
    @include mkdfRelativeHolderLayout(top);
    margin: 49px 0 49px;
    clear: both;

    ul {
        @include mkdfRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;

            a {
                font-family: 'Amatic SC', sans-serif;
                font-size: 30px;
                font-weight: 700;
                position: relative;
                display: inline-block;
                vertical-align: top;
                padding: 3px 0 4px 0;
                margin: 0;
            }

            &.mkdf-pag-active {
                margin-right: -7px;
                
                a {
                    color: #fff;
                    @include mkdfSVGbg();
                    padding: 3px 10px 4px 6px;

                    .mkdf-active-digit {
                        position: relative;
                    }
                }

                &:after {
                    left: -7px;
                }
            }

            &:not(.mkdf-pag-active) {

                a {

                    .mkdf-active-hover {
                        display: none;
                    }
                }
            }

            &.mkdf-pag-number {

                &:after {
                    content: '...';
                    font-family: 'Amatic SC', sans-serif;
                    font-weight: 700;
                    font-size: 30px;
                    color: $default-heading-color;
                    position: relative;
                    top: -3px;
                }
            }

            &.mkdf-pag-prev,
            &.mkdf-pag-next,
            &.mkdf-pag-first,
            &.mkdf-pag-last {
                margin: 0 2px;

                a {
                    font-family: 'Amatic SC', sans-serif;
                    font-weight: 700;
                    font-size: 30px;
                    color: $default-heading-color;
                }

                &:hover {

                    a {
                        color: $first-main-color;
                    }
                }
            }

            &.mkdf-pag-prev {
                margin: 0 18px 0 0;
            }

            &.mkdf-pag-next {
                margin: 0 0 0 18px;
            }

            &.mkdf-pag-last {
                margin: 0 0 0 8px;
            }

            &.mkdf-pag-first {
                margin: 0 8px 0 0;
            }


            @if ($list_type == 'shortcode') {
                
                &.mkdf-pag-prev {
                    
                    a {
                        opacity: 0;
                    }
                }

                &.mkdf-pag-next {
                    
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {

                span.page-numbers.current {

                    span.mkdf-active-digit-holder {
                        font-family: 'Amatic SC', sans-serif;
                        font-size: 30px;
                        font-weight: 700;
                        color: #fff;
                        position: relative;
                        display: inline-block;
                        vertical-align: top;
                        margin: 0;
                        padding: 3px 10px 4px 6px;
                        @include mkdfSVGbg();

                        .mkdf-active-digit {
                            position: relative;
                        }
                    }

                    &:after {
                        content: '...';
                        font-family: 'Amatic SC', sans-serif;
                        font-weight: 700;
                        font-size: 30px;
                        color: $default-heading-color;
                        position: relative;
                        top: -3px;
                    }
                }

                a.page-numbers {

                    span.mkdf-active-hover {
                        display:none;
                    }

                    &:not(.prev):not(.next) {

                        &:after {
                            content: '...';
                            font-family: 'Amatic SC', sans-serif;
                            font-weight: 700;
                            font-size: 30px;
                            color: $default-heading-color;
                            position: relative;
                            top: 0;
                        }
                    }
                }

                a.next {
                    margin: 0 0 0 18px;
                }

                a.prev {
                    margin: 0 18px 0 0;
                }
            }

            &:last-child {

                span.page-numbers.current {

                    &:after {
                        content: none;
                    }
                }
            }
        }
    }

    @include phone-landscape {

        ul {

            li {

                &.mkdf-pag-number {

                    &:after {
                        content: '..';
                    }
                }

                &.mkdf-pag-prev {
                    margin: 0 12px 0 0;
                }

                &.mkdf-pag-next {
                    margin: 0 0 0 12px;
                }

                &:last-child {

                    &:after {
                        content: none;
                    }
                }
            }
        }
    }
}

@mixin mkdfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: $default-heading-font;
    font-size: 20px;
    line-height: 1.2em;
    font-weight: 700;
    outline: none;
    box-sizing: border-box;
    transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out, transform .3s cubic-bezier(0.19, 0.54, 0.25, 1), background-position .3s;
    border-radius: 4px;
}

@mixin mkdfButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 12px 24px;

    } @else if ($size == 'medium') {
        padding: 14px 36px;

    } @else if ($size == 'large') {
        padding: 17px 48px;

    } @else if ($size == 'huge') {
        padding: 20px 60px;
    }
}

@mixin mkdfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin mkdfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border: 1px solid transparent $important;
}

@mixin mkdfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
}

@mixin mkdfButtonOutlineColor($important: null) {
    color: $first-main-color $important;
    background-color: transparent $important;
    border: 2px solid $first-main-color $important;
}

@mixin mkdfButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border-color: $first-main-color $important;
}

@mixin mkdfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

