// mixins styles - start

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 #{$input-margin}px;
    padding: #{$input-vertical-padding}px #{$input-horizontal-padding}px;
    font-family: inherit;
    font-size: 15px;
    font-weight: inherit;
    line-height: 25px;
    color: $default-heading-color;
    background-color: $additional-background-color;
    border: 1px solid transparent;
    border-radius: 4px;
    outline: 0;
    cursor: pointer;
    -webkit-appearance: none;
    box-sizing: border-box;
    transition: border-color 0.2s ease-in-out;

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include mkdfPlaceholder {
        color: inherit;
    }
}

// mixins styles - end

