// heading mixins - start

@mixin mkdfH1() {
    font-size: 57px;
    line-height: 1.053em;
    color: #424242;
}

@mixin mkdfH2() {
    font-size: 50px;
    line-height: 1.1em;
    color: #424242;
}

@mixin mkdfH3() {
    font-size: 40px;
    line-height: 1.2em;
    color: #424242;
}

@mixin mkdfH4() {
    font-size: 30px;
    line-height: 1.2em;
    color: #4a4a4a;
}

@mixin mkdfH5() {
    font-size: 24px;
    line-height: 1.167em;
    color: #4a4a4a;
}

@mixin mkdfH6() {
    font-size: 16px;
    line-height: 1.188em;
    color: #4a4a4a;
}

@mixin mkdfHeadingStyle($with-heading: null) {

    @if ($with-heading == 'h1') {
        @include mkdfH1();
    } @else if ($with-heading == 'h2') {
        @include mkdfH2();
    } @else if ($with-heading == 'h3') {
        @include mkdfH3();
    } @else if ($with-heading == 'h4') {
        @include mkdfH4();
    } @else if ($with-heading == 'h5') {
        @include mkdfH5();
    } @else if ($with-heading == 'h6') {
        @include mkdfH6();
    }
}

@mixin mkdfDefaultHeadingStyle() {
    @include mkdfHeadingStyle();
    font-family: $default-heading-font;
    font-weight: 900;
    margin: 25px 0;

    -ms-word-wrap: break-word;
    word-wrap: break-word;

    a {
        @include mkdfTypographyLayout();

        &:hover {
            color: $first-main-color;
        }
    }
}

@mixin mkdfBody() {
    font-family: $default-text-font;
    font-size: 15px;
    font-weight: 400;
    line-height: 25px;
    color: $default-text-color;
    background-color: $default-background-color;
    -webkit-font-smoothing: antialiased;
}

@mixin mkdfDate() {
    font-family: "Amatic SC", sans-serif;
    font-size: 25px;
    font-weight: 700;
    color: #8f9093;
    line-height: 1em;
    letter-spacing: 1px;
}
// heading mixins - end

